/* eslint-disable array-callback-return */
import { useEffect, useState } from "react";
import { useContext } from "react";
import { useNavigate } from "react-router-dom";

//Components
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import "../../index.css";
import { Container } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';


//dependencies
import Context from "../context/UserContext";
import * as serieServer from "../../services/serie";
import SerieModal from "./serieModal";
import { useModal } from "../../hooks/useModal";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css'





const SerieList = ({series}) => {
  const history = useNavigate();

  const [show, handleShow, handleClose] = useModal(false);
  const [id, setId] = useState("");
  const [currentItem, setCurrentItem] = useState({});
  const [listSeries, setSeries] = useState(series);
  const initialFormData = {serie:"", description:"", quantity:0};
  const [newData, setFormData] = useState(initialFormData);
  const { user } = useContext(Context);

  const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(0),
        marginBottom: theme.spacing(10),
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
    },
    title1: {
      textAlign: "center",
      color: "white"
    }
  }));

  
  const editar = async (item) => {
    setId(item.id);
    setCurrentItem(item);
    setFormData({ serie: item.serie, description:item.description, quantity:item.quantity});
    handleShow(true)
  };

  const registrar = async () => {
    setId(null);
    setCurrentItem({});
    setFormData({serie:"", description:"", quantity:0});
    handleShow(true)
  };


  const getSeries = async () => {
    try {
      const res = await serieServer.ListSeries();
      const data = await res;
      console.log(data);
      setSeries(data);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    if (user && user.is_superuser) {
      getSeries();
      setId(null)
    } else {
    history("/loginuser");
      
    }
    
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const classes = useStyles();
  
  if (user) {
    
    return (
      <Container> 
        
        <div className={classes.paper}>
              <h1 className="mt-4 text-center">Series</h1>
        </div>
        <Stack  alignItems="center">
          <Button 
          variant="contained" 
          color="primary"
          type="submit"
          onClick={()=>registrar()}>
            Registrar Series
          </Button>
        </Stack>
        <br/><br/> <br/>     
        <div>
          <section className="content">
          <div className="table-responsive table-condensed table-sm tabla">
            <table
              className="table table-hover"
              id="dataTableData"
              name="dataTableData"
              style={{ fontSize: 11, textAlign: "center" }}
            >
              <thead className={classes.title1}>
                <tr>
                  <th style={{ display: "none" }}>Id</th>
                  <th>Codigo</th>
                  <th>Serie</th>
                  <th>Descripción</th>
                  <th>Cantidad de videos</th>
                  <th>Imagen</th>
                  <th></th>
                </tr>
              </thead>
              <tbody style={{ backgroundColor: "white" }}>
                {listSeries.length === 0 ? (
                  <tr>
                    <td colSpan={11}>No hay datos</td>
                  </tr>
                ) : (
                  listSeries.map((serie, index) => (
                    <tr key={index}>
                      <td style={{ display: "none" }}>{serie.id}</td>
                      <td>{serie.id}</td>
                      <td>{serie.serie}</td>
                      <td>{serie.description}</td>
                      <td>{serie.quantity}</td>
                      <td><LazyLoadImage effect="blur"    src={"https://sccotview.com"+serie.featured_image} alt="" className="img-fluid img-thumbnail"/></td>
                      <td>
                        <button
                          className="btn btn-outline-primary p-1 btn-sm float-right"
                          type="submit"
                          onClick={()=>editar(serie)}
                        >
                          editar
                        </button>
                        
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        </section>
        </div>
        <SerieModal
          handleClose={handleClose}
          show={show}
          serie_id ={id}
          currentItem={currentItem}
          setSeries = {setSeries}
          setFormData = {setFormData}
          newData={newData}
        ></SerieModal>
      </Container>
    );
  }
  return (
    <p style={{ fontSize: "25px" }}>Inicia sesión para ver todos los videos!</p>
  );
};

export default SerieList;
