import { useState } from "react";
//dependencias
import * as serieServer from "../../services/serie";

//components
import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import { makeStyles } from "@material-ui/core/styles";
import "../../styles/cloud.css";


const useStyles = makeStyles((theme) => ({
  
  containerModal:{
    border: "2px solid #ccc",
    borderRadius: 50,
    boxShadow: "4px 4px 4px 0px #5a28e550",
  },
  containerForm: {
    height:"3rem",
    background: "#BFF",
    border: "2px solid #ccc",
    borderRadius: 50,
    boxShadow: "4px 4px 4px 0px #5a28e550",
  },

  "@media (max-width: 720px)": {
    root: {
      display: "flex",
    },
  },
}));
const ModalSerie = ({ handleClose, show, serie_id, currentItem, setFormData, newData, ...props}) => {
  const [selectedFileSerie,setSelectedFileSerie] = useState(null)
  
  const handleInputChange = (e) => {
    let target = e.target;
    let name = target.name;
    setFormData({...newData, [name]: e.target.value });
  };
  const handleInputChangeImg = (e) => {
    let target = e.target;
    let name = target.name;
    if (name === "imgSerie") {
      setSelectedFileSerie(e.target.files[0]);
    }
   
    
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const formData = new FormData();
    console.log(selectedFileSerie);
    try {
      if (!serie_id) {
        formData.append("serie", newData.serie);
        formData.append("description", newData.description);
        formData.append("quantity", newData.quantity);
        if(selectedFileSerie){
          formData.append("featured_image", selectedFileSerie);
        }

        await serieServer.RegisterSerie(formData);
      } else {
        formData.append("serie", newData.serie);
        formData.append("description", newData.description);
        formData.append("quantity", newData.quantity);
        if(selectedFileSerie){
          formData.append("featured_image", selectedFileSerie);
        }
        await serieServer.updateSerie(serie_id, formData);        
      }
      const res = await serieServer.ListSeries();
      const data = await res;
      console.log(data);
      console.log(formData);
      props.setSeries(data);
    } catch (error) {
      console.log(error);      
    }
  }
  
  const classes = useStyles();
  return (
    <Modal 
      show={show} 
      onHide={handleClose}
      aria-labelledby="example-custom-modal-styling-title"
      contentClassName = {classes.cloud}
      centered
    >
      <Modal.Header closeButton >
        <Modal.Title id="tituloModal" ><h2>{serie_id? 'Editar Registro' : 'Crear Registro'}</h2></Modal.Title>
      </Modal.Header>
      <Modal.Body id="bodyModal" >
        <Form onSubmit={handleSubmit}  >
          <Form.Group className={"mb-2 "} controlId="exampleForm.ControlInput1">
            <Form.Control type="text"
            name="serie"
            placeholder="Nombre de la serie"
            value={newData.serie || ""}
            onChange={handleInputChange} 
            />
            
            <br />
            <Form.Control as="textarea" rows={3}
            name="description"
            placeholder="Descripción de la serie"
            value={newData.description || ""}
            onChange={handleInputChange} 
            />
            <br />
            <Form.Control type="number"
            name="quantity"
            placeholder="Cantidad de videos"
            value={newData.quantity || null}
            onChange={handleInputChange} 
            />
            <input type="file" className="form-control mt-3" name="imgSerie" onChange={handleInputChangeImg}  />
          </Form.Group>
          <Modal.Footer>
            <Button variant="secondary" onClick={handleClose}>
              Close
            </Button>
            <Button variant="primary" type="submit" onClick={handleClose}>
            {serie_id ? 'Guardar Cambios' : 'Crear'}
            </Button>
          </Modal.Footer>
        </Form>
      </Modal.Body>
    </Modal>
    
  )
};

export default ModalSerie;